
















































































































@import "@/assets/css/common.scss";

.pop-wrapper {
    padding: 15px 20px 0;
    position: relative;

    .tag {
        display: inline-block;
        padding: 0 10px;
        height: 22px;
        line-height: 22px;
        color: #666666;
        cursor: pointer;
        font-size: 14px;

    }

    .tag-select {
        background-color: #13D1BE;
        color: #ffffff;
        border-radius: 10px;
    }

    &:not(:last-of-type) .category-col {
        border-bottom: 1px solid #E5E5E5;
    }

    .filter {
        .title {
            color: #333;
            font-size: 14px;
        }

        .category-col {
            padding-bottom: 10px;

            .category-wrapper {
                height: 28px;
            }
        }

    }

    .m-dialog {
        z-index: 6;
        background-color: #fff;
    }

    .options {
        h3 {
            color: #CCCCCC;
            font-size: 16px;
            margin-bottom: 10px;
            font-weight: 300;
        }

        box-sizing: border-box;
        width: 510px;
        padding: 19px 15px 9px;

        .tag {
            font-size: 12px;
        }
    }
}
